@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Playfair+Display:wght@500&family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Krona+One&family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{
    background-color: #1c1c1c;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    color: #dfdfdf;
}
.text{
    padding:10px
}
.container{
    margin-top: 200px;
}
.name-content{
    font-size: 50px;
    text-transform: uppercase;
    font-weight: 900;
    line-height: 1.5;
    font-family: 'Russo One', sans-serif;
}
.last-name{
    padding-left: 15px;
    color: #FFB739;
}
.level-3{
    color: #FFB739;
    font-family: 'Balsamiq Sans', cursive;
    
}
.avatar{
    position: relative;
    top: -50px;
    width: 230px;
}
.level-4{
    font-family: 'Balsamiq Sans', cursive;
    font-size: 20px;
    color: #bbb4b4;
}
.social-media a{
    font-size: 25px;
    padding-left: 15px;
}
.icons:hover{
    color: #FFB739;
    cursor: pointer;
}
.session-experience{
    margin-top: 200px;
}
.section-title{
    text-transform: uppercase;
    font-family: 'Russo One', sans-serif;
    box-sizing: border-box;
    display: inline-block;
    font-weight: 700;
    text-transform: uppercase;
    padding-bottom: 4px;
    position: relative;
}
.section-title-wrapper .level-2::before{
    width: 100%;
    background-color: rgba(255, 255, 255, 0.3);
    bottom: 1px;
    height: 2px;
    content: '';
    display: block;
    position: absolute;
}
.section-title-wrapper .level-2::after{
    width: 50px;
    max-width: 100%;
    background-color: #FFB739;
    bottom: 0;
    height: 4px;
    content: '';
    display: block;
    position: absolute;
}

.box-sub-title .duration{
    display: inline-block;
    padding: 0 10px;
    margin-right: 10px;
    border: 2px solid #FFB739;
    background-color: #1c1c1c;
    border-radius: 30px;
    color: #f5f5f5;
    font-weight: 400;
}
.box-sub-title .company{
    color: rgb(138, 128, 128);
    text-transform: uppercase;
    font-family: 'Krona One', sans-serif;
}
.box-sub-title{
    margin-left: -50px;
    margin-bottom: 15px;
    
}
.exper-wrapper{
    padding-left: 0px;
    padding-top: 30px;
}
.exper-box{
    padding: 25px;
    border-left: 1px solid #6b6666;
}
.box-title{
    font-family: 'Krona One', sans-serif;
    color: white;
}
.exper-box .content{
    font-size: 16px;
    line-height: 1.5;
    font-weight: 100;
    color: #dfdfdf;
    font-family: 'Montserrat', sans-serif;
}
.content span{
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    color: #dfdfdf;
    display: inline-block;
    padding: 0 5px;
    background-color: rgba(255, 255, 255, 0.1);
    margin-bottom: 3px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
}
.session-Certifications{
    margin-top: 150px;
}
.certifications .logo{
    box-sizing: border-box;
    padding: 15px;
    background-color: #333;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
.certifications{
    display: flex;
    border: 2px solid #333;
    border-radius: 14px;
    line-height: 1.5;
    margin-top: 50px;
}
.certifications .content{
    box-sizing: border-box;
    padding-left: 10px;
    border-color: #333;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
.content-container{
    padding: 13px;
}
.content-container .cert-title{
    font-size: 20px;
    text-align: left;
    font-family: system-ui;
}
.cert-sub-title{
    color: #9b8d8d;
    font-family: emoji;
}
.cert-time{
    padding-top: 8px;
    font-size: 13px;
    font-family: monospace;
}

.session-technical .section-content{
    text-align: center;
}
.skill .pie-wrapper .icon{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 6px solid rgba(255, 255, 255, 0.07);
    display: flex;
    justify-content: center;
    border-width: 8.33333px;
    border-radius: 50%;
    
}
.skill{
    margin: 20px 10px;
    text-align: center;
    justify-content: center;
    display: inline-block;

}
.pie-wrapper{
    position: relative;
    height: 125px;
    width: 125px;
    margin-bottom: 10px;
}

.skill .pie{
    clip: rect(auto, auto, auto, auto);
    left: 0;
    top: 0;
    position: absolute;
    height: 100%;
    width: 100%;
} 

.skill .pie-wrapper .half-circle{
    border: 6px solid;
    left: 0;
    top: 0;
    position: absolute;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    height: 100%;
    width: 100%;
    border-width: 8.33333px;
    border-color: #fe4c1e;
    clip: rect(0, 62.5px, 125px, 0);
} 
.skill .pie-wrapper .left-side{
    transform: rotate(306deg);
    
}
.skill .pie-wrapper .right-side{
    transform: rotate(180deg);
}

.icon-item{
    
    position: absolute;
    top: 25%;
    height: 60px;
    width: 60px;
}
